.datepicker__cells {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap
}

.datepicker__cell {
  border-radius: 50%;
  box-sizing: border-box;
  font-family: MontseratRegular;
  cursor: pointer;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: 40px;
  z-index: 1
}

.datepicker__cell.-focus- {
  background: transparent;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    border-radius: 50%;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $purple_gradient;
    opacity: 0.25;
  }
}

.datepicker__cell.-current- {
  color: #fff;
  font-family: MontseratBold;
  background: $green_gradient;
}

.datepicker__cell.-current-.-focus- {
  box-shadow: inset 0 0 40px rgba(255, 255, 255, 0.2);
}

.datepicker__cell.-in-range- {
  background: transparent;
  position: relative;
  color: #4a4a4a;
  border-radius: 0;

  &::after {
    content: '';
    position: absolute;
    z-index: 0;
    border-radius: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $purple_gradient;
    opacity: 0.25;
  }
}

.datepicker__cell.-in-range-.-focus- {
  background-color: rgba(92, 196, 239, .2)
}

.datepicker__cell.-disabled- {
  cursor: default;
  color: #aeaeae
}

.datepicker__cell.-disabled-.-focus- {
  color: #aeaeae
}

.datepicker__cell.-disabled-.-in-range- {
  color: #a1a1a1
}

.datepicker__cell.-disabled-.-current-.-focus- {
  color: #aeaeae
}

.datepicker__cell.-range-from- {
  border-radius: 50%;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    border-radius: 0;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: $purple_gradient;
    opacity: 0.25;
  }

  &.-selected- {

    &::after {
      width: 50%;
    }
  }

  &.-range-to- {

    &::after {
      display: none;
    }
  }
}

.datepicker__cell.-range-to- {
  border-radius: 50%;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    border-radius: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $purple_gradient;
    opacity: 0.25;
  }

  &.-selected- {

    &::after {
      width: 50%;
    }
  }
}

.datepicker__cell.-selected-,
.datepicker__cell.-selected-.-current- {
  font-family: MontseratBold;
  color: #fff;
  background: $purple_gradient
}

.datepicker__cell.-selected-.-focus- {
  box-shadow: inset 0 0 40px rgba(255, 255, 255, 0.2);
}

.datepicker__cell:empty {
  cursor: default
}

.datepicker__days-names {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin: 5px 0 3px
}

.datepicker__day-name {
  color: $purple_main;
  display: flex;
  height: 40px;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.-only-timepicker- .datepicker__content,
.datepicker__body,
.datepicker-inline .datepicker__pointer {
  display: none
}

.datepicker__cell-day {
  width: 14.28571%
}

.datepicker__cells-months {
  height: 170px
}

.datepicker__cell-month {
  width: 50px;
  height: 50px
}

.datepicker__cells-years,
.datepicker__years {
  height: 170px
}

.datepicker__cell-year {
  width: 50px;
  height: 50px
}

.datepickers-container {
  position: absolute;
  left: 0;
  top: 0
}

@media print {
  .datepickers-container {
    display: none
  }
}

.datepicker {
  background: #fff;
  border: 1px solid $dark_shade_25;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  border-radius: 4px;
  box-sizing: border-box;
  font-family: QuicksandRegular, sans-serif;
  font-size: 14px;
  color: #4a4a4a;
  width: 320px;
  padding: 20px;
  position: absolute;
  left: -100000px;
  opacity: 0;
  transition: opacity .3s ease, left 0s .3s, -webkit-transform .3s ease;
  transition: opacity .3s ease, transform .3s ease, left 0s .3s;
  transition: opacity .3s ease, transform .3s ease, left 0s .3s, -webkit-transform .3s ease;
  z-index: 100
}

.datepicker.-from-top- {
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px)
}

.datepicker.-from-right- {
  -webkit-transform: translateX(5px);
  transform: translateX(5px)
}

.datepicker.-from-bottom- {
  transform: translateY(5px)
}

.datepicker.-from-left- {
  -webkit-transform: translateX(-5px);
  transform: translateX(-5px)
}

.datepicker.active {
  opacity: 1;
  -webkit-transform: translate(0);
  transform: translate(0);
  transition: opacity .3s ease, left 0s 0s, -webkit-transform .3s ease;
  transition: opacity .3s ease, transform .3s ease, left 0s 0s;
  transition: opacity .3s ease, transform .3s ease, left 0s 0s, -webkit-transform .3s ease
}

.datepicker-inline .datepicker {
  border-color: #d7d7d7;
  box-shadow: none;
  position: static;
  left: auto;
  right: auto;
  opacity: 1;
  -webkit-transform: none;
  transform: none
}

.datepicker__content {
  box-sizing: content-box;
}

.datepicker__pointer {
  position: absolute;
  background: #fff;
  border-top: 1px solid #dbdbdb;
  border-right: 1px solid #dbdbdb;
  width: 10px;
  height: 10px;
  z-index: -1
}

.datepicker__nav-action:hover,
.datepicker__nav-title:hover {
  background: #f0f0f0
}

.-top-center- .datepicker__pointer,
.-top-left- .datepicker__pointer,
.-top-right- .datepicker__pointer {
  top: calc(100% - 4px);
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg)
}

.-right-bottom- .datepicker__pointer,
.-right-center- .datepicker__pointer,
.-right-top- .datepicker__pointer {
  right: calc(100% - 4px);
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg)
}

.-bottom-center- .datepicker__pointer,
.-bottom-left- .datepicker__pointer,
.-bottom-right- .datepicker__pointer {
  bottom: calc(100% - 4px);
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg)
}

.-left-bottom- .datepicker__pointer,
.-left-center- .datepicker__pointer,
.-left-top- .datepicker__pointer {
  left: calc(100% - 4px);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg)
}

.-bottom-left- .datepicker__pointer,
.-top-left- .datepicker__pointer {
  left: 10px
}

.-bottom-right- .datepicker__pointer,
.-top-right- .datepicker__pointer {
  right: 10px
}

.-bottom-center- .datepicker__pointer,
.-top-center- .datepicker__pointer {
  left: calc(50% - 10px / 2)
}

.-left-top- .datepicker__pointer,
.-right-top- .datepicker__pointer {
  top: 10px
}

.-left-bottom- .datepicker__pointer,
.-right-bottom- .datepicker__pointer {
  bottom: 10px
}

.-left-center- .datepicker__pointer,
.-right-center- .datepicker__pointer {
  top: calc(50% - 10px / 2)
}

.datepicker__body.active {
  display: block
}

.datepicker__nav {
  display: flex;
  justify-content: space-between;
  min-height: 32px;
}

.-only-timepicker- .datepicker__nav {
  display: none
}

.datepicker__nav-action,
.datepicker__nav-title {
  display: flex;
  justify-content: end;
  align-items: center;
  cursor: pointer;
}

.datepicker__nav-action {
  width: 20px;
  border-radius: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none
}

.datepicker__nav-action.-disabled- {
  visibility: hidden
}

.datepicker__nav-action i {
  width: 20px;
  height: 20px;
  color: $purple_main;
}

.datepicker__nav-title {
  font-family: QuicksandBold;
  border-radius: 4px;
}

.datepicker__time {
  padding: 4px
}

.datepicker__nav-title i {
  margin-left: 5px
}

.datepicker__nav-title.-disabled- {
  cursor: default;
  background: 0 0
}

.datepicker__buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.datepicker__button {
  padding: 0;
  color: $dark_shade_50;
  border: none;
  background-color: transparent;
  text-transform: uppercase;
  font-family: MontseratBold;
  font-size: 12px;
  cursor: pointer;
  outline: none;

  &[data-action="accept"] {
    color: $purple_main;
  }
}

.datepicker__button:hover {
  color: $dark_shade_75;

  &[data-action="accept"] {
    color: $purple_main;
  }
}

.datepicker__time {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative
}

.datepicker__time.-am-pm- .datepicker__time-sliders {
  -webkit-flex: 0 1 135px;
  -ms-flex: 0 1 135px;
  flex: 0 1 135px;
  max-width: 135px
}

.-only-timepicker- .datepicker__time {
  border-top: none
}

.datepicker__time-sliders {
  -webkit-flex: 0 1 153px;
  -ms-flex: 0 1 153px;
  flex: 0 1 153px;
  margin-right: 10px;
  max-width: 153px
}

.datepicker__time-label {
  display: none;
  font-size: 12px
}

.datepicker__time-current {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  font-size: 14px;
  text-align: center;
  margin: 0 0 0 10px
}

.datepicker__time-current-colon {
  margin: 0 2px 3px;
  line-height: 1
}

.datepicker__time-current-hours,
.datepicker__time-current-minutes {
  line-height: 1;
  font-size: 19px;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  position: relative;
  z-index: 1
}

.datepicker__time-current-hours:after,
.datepicker__time-current-minutes:after {
  content: '';
  background: #f0f0f0;
  border-radius: 4px;
  position: absolute;
  left: -2px;
  top: -3px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
  opacity: 0
}

.datepicker__time-current-hours.-focus-:after,
.datepicker__time-current-minutes.-focus-:after {
  opacity: 1
}

.datepicker__time-current-ampm {
  text-transform: uppercase;
  -webkit-align-self: flex-end;
  -ms-flex-item-align: end;
  align-self: flex-end;
  color: #9c9c9c;
  margin-left: 6px;
  font-size: 11px;
  margin-bottom: 1px
}

.datepicker__time-row {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 11px;
  height: 17px;
  background: linear-gradient(to right, #dedede, #dedede) left 50%/100% 1px no-repeat
}

.datepicker__time-row:first-child {
  margin-bottom: 4px
}

.datepicker__time-row input[type=range] {
  background: 0 0;
  cursor: pointer;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  height: 100%;
  padding: 0;
  margin: 0;
  -webkit-appearance: none
}

.datepicker__time-row input[type=range]::-ms-tooltip {
  display: none
}

.datepicker__time-row input[type=range]:hover::-webkit-slider-thumb {
  border-color: #b8b8b8
}

.datepicker__time-row input[type=range]:hover::-moz-range-thumb {
  border-color: #b8b8b8
}

.datepicker__time-row input[type=range]:hover::-ms-thumb {
  border-color: #b8b8b8
}

.datepicker__time-row input[type=range]:focus {
  outline: 0
}

.datepicker__time-row input[type=range]:focus::-webkit-slider-thumb {
  background: #5cc4ef;
  border-color: #5cc4ef
}

.datepicker__time-row input[type=range]:focus::-moz-range-thumb {
  background: #5cc4ef;
  border-color: #5cc4ef
}

.datepicker__time-row input[type=range]:focus::-ms-thumb {
  background: #5cc4ef;
  border-color: #5cc4ef
}

.datepicker__time-row input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  box-sizing: border-box;
  height: 12px;
  width: 12px;
  border-radius: 3px;
  border: 1px solid #dedede;
  background: #fff;
  cursor: pointer;
  transition: background .2s;
  margin-top: -6px
}

.datepicker__time-row input[type=range]::-moz-range-thumb {
  box-sizing: border-box;
  height: 12px;
  width: 12px;
  border-radius: 3px;
  border: 1px solid #dedede;
  background: #fff;
  cursor: pointer;
  transition: background .2s
}

.datepicker__time-row input[type=range]::-ms-thumb {
  box-sizing: border-box;
  height: 12px;
  width: 12px;
  border-radius: 3px;
  border: 1px solid #dedede;
  background: #fff;
  cursor: pointer;
  transition: background .2s
}

.datepicker__time-row input[type=range]::-webkit-slider-runnable-track {
  border: none;
  height: 1px;
  cursor: pointer;
  color: transparent;
  background: 0 0
}

.datepicker__time-row input[type=range]::-moz-range-track {
  border: none;
  height: 1px;
  cursor: pointer;
  color: transparent;
  background: 0 0
}

.datepicker__time-row input[type=range]::-ms-track {
  border: none;
  height: 1px;
  cursor: pointer;
  color: transparent;
  background: 0 0
}

.datepicker__time-row input[type=range]::-ms-fill-lower {
  background: 0 0
}

.datepicker__time-row input[type=range]::-ms-fill-upper {
  background: 0 0
}

.datepicker__time-row span {
  padding: 0 12px
}

.datepicker__time-icon {
  color: #9c9c9c;
  border: 1px solid;
  border-radius: 50%;
  font-size: 16px;
  position: relative;
  margin: 0 5px -1px 0;
  width: 1em;
  height: 1em
}

.datepicker__time-icon:after,
.datepicker__time-icon:before {
  content: '';
  background: currentColor;
  position: absolute
}

.datepicker__time-icon:after {
  height: .4em;
  width: 1px;
  left: calc(50% - 1px);
  top: calc(50% + 1px);
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%)
}

.datepicker__time-icon:before {
  width: .4em;
  height: 1px;
  top: calc(50% + 1px);
  left: calc(50% - 1px)
}

.datepicker__cell-day.-other-month-,
.datepicker__cell-year.-other-decade- {
  color: $dark_shade_25;

  &.-selected- {
    color: #fff;
  }

  &.-current- {
    color: #fff;
  }

  &.-in-range- {

    &.-current- {
      color: $green_main;
    }
  }
}

