.dropdowns-container {
  position: absolute;
  left: 0;
  top: 0
}

.dropdown {
  position: absolute;
  box-sizing: border-box;
  padding: 7px 7px 13px 15px;
  background: #fff;
  border: 1px solid $dark_shade_50;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  left: -100000px;
  z-index: 100;
  opacity: 0;
  transition: opacity .3s ease, transform .3s ease, left 0s .3s;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);

  &.active {
    opacity: 1;
    transition: opacity .3s ease, transform .3s ease, left 0s 0s;
  }

  &__fields {
    display: flex;
    flex-direction: column;
  }

  &__field {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    &:not(:first-child) {
      margin-top: 7px;
    }
  }

  &__counter {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 92px;

    &-inc,
    &-dec {
      cursor: pointer;
      outline: none;
      width: 30px;
      height: 30px;
      background: transparent;
      color: $dark_shade_50;
      border: 1px solid $dark_shade_25;
      border-radius: 50px;
      font-size: 18px;
      display: inline-flex;
      align-items: center;
      padding: 0;
      justify-content: center;

      &.disabled {
        color: $dark_shade_5;
        border: 1px solid $dark_shade_5;
      }
      
    }

    &-value {
      font-family: MontseratBold;
    }

    &-label{
      text-transform: uppercase;
      font-family: MontseratBold;
      font-size: 12px;
    }
  }

  &__buttons {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
  }
  
  &__button {
    padding: 0;
    color: $dark_shade_50;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    font-family: MontseratBold;
    font-size: 12px;
    cursor: pointer;
    outline: none;
  
    &[data-action="accept"] {
      color: $purple_main;
    }
  }
}